* {
  margin: 0;
  padding: 0;
}
.App {
  font-family: sans-serif;
  /* text-align: center; */
  background-color: #f59e0b;
}
.fruitList {
  list-style: none;
}
.primary-btn {
  color: whitesmoke;
  background-color: #8ab082;
  padding: 10px;
  border-style: none;
  border-radius: 4px;
  text-align: center;
  cursor: pointer;
  margin: 10px;
}
.secondary-btn {
  color: #8ab082;
  border: 1px solid #8ab082;
  padding: 10px;
  border-radius: 5px;
  background-color: whitesmoke;
  text-align: center;
  transition-duration: 0.4s;
  cursor: pointer;
  margin: 10px;
}

.secondary-btn:hover {
  background-color: #8ab082;
  color: whitesmoke;
}
.list {
  border-style: none;
  border-radius: 0.2rem;
  padding: 0.5rem;
  text-align: center;
  background-color: #fde68a;
  margin: 1rem;
  font-size: 1.5rem;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
.price {
  font-size: 1rem;
  margin: 0.5rem;
}
.welcome-text {
  text-align: center;
  color: white;
  padding: 0.5rem;
  font-family: "Times New Roman", Times, serif;
  font-size: 2.5rem;
}
.input-textArea {
  margin: 0.5rem;
  background-color: #fef3c7;
  border: 1px solid transparent;
  padding: 0.5rem;
  border-radius: 20px;
  color: #8ab082;
}
.input-div {
  /* background-color: #8ab082; */
  max-width: 50%;
  display: flex;
}
